.login{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    height: 100vh;
}

.login__logo{
    width: 100px;
    object-fit: contain;
    margin-top: 20px;
    margin-bottom: 20px;
}

.login__container{
    display: flex;
    width: 300px;
    flex-direction: column;
    padding: 20px;
    border: 1px solid lightgray;
}

.login__signInButton, .login__registerButton{
    background-color: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    height: 30px;
    border-radius: 2px;
    color:#111;
    border-color: #a88734 #9c7e31 #846a29;
}

.login__container p{
    margin-top: 15px;
    font-size: 12px;
}

.login__container > h1{
    font-weight:500;
    margin-bottom: 20px;
}

.login__container > form > h5 {
    margin-bottom: 5px;

}

.login__container > form > input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;
}

.a-divider-break{
    text-align: center;
    padding-top:5px;
    font-weight: 100;
    color:rgb(146, 143, 143);
}